// src/features/auth/auth-context.jsx
"use client"

import * as React from "react"

import { authenticateWithOpenId } from "./api/openid"

const AuthContext = React.createContext(null)

export function AuthProvider({ children }) {
  const [user, setUser] = React.useState(null)
  const [status, setStatus] = React.useState("unauthenticated")
  const [error, setError] = React.useState(null)

  const signIn = React.useCallback(async (payload = {}) => {
    setStatus("loading")
    setError(null)

    try {
      const result = await authenticateWithOpenId(payload)
      const authenticatedUser = result?.user ?? null

      if (!authenticatedUser) {
        throw new Error("인증 응답에 사용자 정보가 없습니다.")
      }

      setUser(authenticatedUser)
      setStatus("authenticated")
      return result
    } catch (err) {
      setStatus("error")
      setError(err)
      throw err
    }
  }, [])

  const signOut = React.useCallback(() => {
    setUser(null)
    setStatus("unauthenticated")
    setError(null)
  }, [])

  const value = React.useMemo(
    () => ({
      user,
      status,
      error,
      signIn,
      signOut,
      setUser,
    }),
    [user, status, error, signIn, signOut],
  )

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = React.useContext(AuthContext)

  if (!context) {
    throw new Error("useAuth 훅은 AuthProvider 내부에서만 사용할 수 있습니다.")
  }

  return context
}
